import Vue from 'vue'
import Router from 'vue-router'
import no1 from './no1.vue'
import no2 from './no2.vue'
import no3 from './no3.vue'
import no4 from './no4.vue'
import no5 from './no5.vue'
import a from './a.vue'
import gwc from './gwc.vue'
import syt from './syt.vue'
Vue.use(Router)

var router = new Router({
	routes:[
		{path:'/',name:'no1',component:no1},
		{path:'/no2',name:'no2',component:no2},
		{path:'/no3',name:'no3',component:no3},
		{path:'/no4',name:'no4',component:no4},
		{path:'/no5',name:'no5',component:no5},
		{path:'/a',name:'a',component:a},
		{path:'/gwc',name:'gwc',component:gwc},
		{path:'/syt',name:'syt',component:syt}
	]
})
export default router


