				<li><span>{{i.tittle}}</span></li>
				<li><i>¥{{i.yj}}</i></li>
				<li>
					<div class="jans" @click="a_jian(i)">-</div>
					<input :value="i.qian" type="text" value="1" />
					<div @click="a_jia(i)" class="jias">+</div>
				</li>
				<li class="jgs">¥{{i.qian*i.xj}}</li>
				<li @click="a_del($index)"><a href="#" >删除</a></li>
			</ul>
			<ul style="margin-top:35px ;" class="sp">
				<p><span>运费：</span>普通快递</p>
				<p ><span>订单总计：</span>249</p>
