<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		data(){
				return{
					index:0
				}
		},
			computed:{
				...mapGetters(['g_arr2'])
			},
			methods:{
				...mapActions(['a_del','a_jia','a_jian']),
				jia(a){
					console.log(a)
					this.g_gwc[a].qian++
				},
				jian(a){
					this.index=a
					if(this.g_gwc[a].qian<=1){
						this.g_gwc[a].qian=1
					}else{
						this.g_gwc[a].qian--
					}
				}
			},
			ji(){
				console.log(num)
				var num = 0
				for(var i = 0;i<this.g_gwc.length;i++){
					num += Number(this.g_gwc[i].qian*this.g_gwc[i].xj)
				}
				return num
			}
		}
</script>

